import {
  Component,
  OnInit,
  Input,
  HostBinding,
  OnDestroy,
  ElementRef,
  HostListener,
  Inject,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
} from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { InputNumber } from '@delon/util';

import { ErrorCollectConfig } from './error-collect.config';

/**
 * éè¯¯æ¶æ¯ééå¨
 * PSï¼è½ç¶æ­¤æ³å¹¶ä¸å¥½çï¼ä½å¯¹ååºå¼è¡¨å&æ¨¡æ¿è¡¨åæå¾å¥½çææã
 */
@Component({
  selector: 'error-collect, [error-collect]',
  template: `
  <i class="anticon anticon-exclamation-circle"></i>
  <span class="pl-sm">{{count}}</span>`,
  host: { '[class.error-collect]': 'true' },
  changeDetection: ChangeDetectionStrategy.OnPush,
  preserveWhitespaces: false,
})
export class ErrorCollectComponent implements OnInit, OnDestroy {
  private $time = null;
  private formEl: HTMLFormElement;

  @Input()
  @InputNumber()
  freq: number;

  @Input()
  @InputNumber()
  offsetTop: number;

  @HostBinding('class.d-none')
  _hiden = true;

  count = 0;

  constructor(
    cog: ErrorCollectConfig,
    private el: ElementRef,
    private cd: ChangeDetectorRef,
    @Inject(DOCUMENT) private doc: any,
  ) {
    Object.assign(this, cog);
  }

  private get errEls() {
    return this.formEl.querySelectorAll('.has-error');
  }

  private update() {
    const count = this.errEls.length;
    if (count === this.count) return;
    this.count = count;
    this._hiden = count === 0;
    this.cd.markForCheck();
  }

  @HostListener('click')
  _click() {
    if (this.count === 0) return false;
    // nz-form-control
    const els = this.errEls;
    const formItemEl = this.findParent(els[0], '[nz-form-control]') || els[0];
    formItemEl.scrollIntoView(true);
    // fix header height
    this.doc.documentElement.scrollTop -= this.offsetTop;
  }

  private install() {
    this.uninstall();
    this.$time = setInterval(() => this.update(), this.freq);
    this.update();
  }

  private uninstall() {
    clearInterval(this.$time);
  }

  private findParent(el: any, selector: string) {
    let retEl = null;
    while (el) {
      if (el.querySelector(selector)) {
        retEl = el;
        break;
      }
      el = el.parentElement;
    }
    return retEl;
  }

  ngOnInit() {
    this.formEl = this.findParent(this.el.nativeElement, 'form');
    if (this.formEl === null) throw new Error('æªæ¾å°ææ form åç´ ');
    this.install();
  }

  ngOnDestroy() {
    this.uninstall();
  }
}
